@font-face {
  font-family: 'dyslexia';
  src: url('../asset/font/open_dyslexic/OpenDyslexicAlta-Regular.otf');
   font-weight: normal;
  font-style: normal;
}
.hidden {
	display:none;
}

body {
	
	position:relative;
	background-color:#FBFAF2;
	max-width:100vw;
	overflow-x:hidden;

	#overlay {
		display: none;
		width: 100%;
		height: calc(100% + 5em);
		z-index: 50;
		position:absolute;
		background-color:rgba(0,0,0,.5);
		.close-checklist {
			// outline: 1px blue solid;
			width: 2em;
			position:absolute;
			top:3em;
			left:50%;
			transform:translate(-50%,0); 
			cursor: hand;
			cursor:pointer;

			img {
				width: 100%;
			}
			
		}
		#checklist {
			width: 80%;
			// height: 100%;
			margin: 8em auto 0;
			background-color:#FBFAF2;
			padding:1em;
			h3 {
				font-weight: 400;
				font-size: .9em;
				text-align: center;
				margin-bottom: 2em;
				color:#E59C00;
				span {
					font-size: .7em;
				}
			}
			.list-wrap {
				display: flex;
				flex-wrap:wrap;
				align-items:center;
				.checkbox {
					flex:1 0 1em;
					font-size: .8em;
					margin:0 0 1em 0;
					color: #888;
					font-weight: 300;
				}
				input {
					flex:0 0 1em;
					margin-bottom: 1em;
					margin-right: .5em;
				}
			}
		}
	}//overlay

	input {
		outline: none;
	}

	h2, h3 {

		margin: 0;
	}
	#login {
		display: none;
		position:absolute;
		z-index: 50;
		width: 100vw;
		height: 100%;
  		background-color: rgba(136,168,183,.97);

		.close-login-box {
			// outline: 1px blue solid;
			width: 3em;
			position:fixed;
			top:2em;
			right:2em;
			cursor: hand;
			cursor:pointer;
			img {
				width: 100%;
			}
		}
		.login-box {
			// outline: 1px red solid;
			width:80%;
			@media only screen and (min-width : 44em) {
				width: 50%;
			}
			position:fixed;
			top:50%;
			left: 50%;
			transform: translate(-50% , -50%);
			input[type="text"]:nth-child(4) {
				margin-bottom: 2em;
			}
			.login-error {
				li {
					font-size:.9em;
					color: tomato;
					font-weight:200;
				}
			}

		}
	}//login
	.generic-form {
		padding:2em;
		display:flex;
		flex-direction:column;
		background-color:#eee;
		h3 {
			
			font-size:2em;
			margin-bottom: 1em;
			color:#555;
			font-weight: 300;
			// outline:1px red solid;
		}
		input[type="text"],input[type="password"] {
			padding:.7em;
			margin-bottom:.5em;
			border:none;
			border-bottom:1px #555 solid;
			font-weight: 300;
			color: #555;
			background-color:rgba(0,0,0,0);
		}
		input[type="text"]:focus,input[type="password"]:focus {
			border-bottom:1px tomato solid;
		}
		::placeholder {
			color: #555;
		}

		input[type="submit"] {
			width:100%;
			margin:0 auto;
			padding:0;
			border:none;
			padding:.5em 0;
			border:1px #555 solid;
			border-radius:5px;
			background-color:rgba(0,0,0,0);
			color: #555;
			font-weight: 100;
		}
	}//generic-form
	header {
		
		height:4.5em;
		.inner {
			@media only screen and (min-width : 75em) {
				width: 72em;
				margin: 0 auto;
			}
			.header-wrap {
				padding-top:1em;
				display:flex;
				flex-wrap:wrap;
				margin:0 auto;
				// element:hover {
				// 	color:rgba(0,0,0,.5);
				// }	
				// element:hover, element.hover_effect {
				//     color:rgba(0,0,0,.5);
				// }	
				// .hover {
				// 	-webkit-user-select: none;
				// 	-webkit-touch-callout: none;        
				// }		
				.logo {
					flex:0 0 5em;
					margin-left:1em;
				}
				.empty, .mobile-button, .search-box {
					@media only screen and (min-width : 64em) {
						display: none;
					}
				}
				.empty {
					flex:1 0 1em;
				}
				.mobile-button {
					flex:0 0 1.5em;
					height:1.5em;
					margin-left:.7em;
					padding:.5em;
					border:.15em #888 solid;
					border-radius:100%;
					cursor:hand;
					cursor:pointer;
				}
				.search {
					margin-right:1em;
				}
				img { 
					width:100%;
				}
				.desktop-menu {
					display: none;
					flex:1 0 1em;
					// border: 1px red solid;
					@media only screen and (min-width : 64em) {
						display: block;
					}
					.desktop-nav {
						// width: ;
						margin:0;
						padding:0;
						height: 50px;
						display:flex;
						flex-wrap: wrap;
						justify-content: flex-end;
						list-style-type: none;
						align-items:center;
						li {
							display: inline-block;
							padding: 0 1em;
							a {
								text-decoration: none;
								color: #000;
								font-weight: 300;
							}
							a:hover {
								color:tomato;
								font-weight: 100;

							}
							&:nth-child(2){
								position:relative;
								#desktop-sub-menu {
									display:none;
									// display: flex;
									flex-direction: column;
									// width: 10em;
									position:absolute;
									top: 2.5em;
									left: 0;
									z-index: 25;
									padding:0;
									background-color: rgba(0,0,0,.5);
									padding: 1em 1em 1em .5em;
									li {
										font-size: .9em;
										line-height: 1.6em;
										
									}

								}
								
							}//&:nth-child(2)
						}//li
						
						.search-desktop {
							// width:100% ;
							width: 7em;
							padding:0;
							margin: 0 1em 0 0;
							display: flex;
							flex-wrap: wrap;
							justify-content: flex-end;

							.search-out-box {
								flex:0 0 1em;
								border:1px #888 solid;
								border-radius:50%;
								padding:.3em;
								img {
										width: 100%;
										display: block;
									}
							}
							form {
								
								flex:1 0 1em;
								display: flex;
								flex-wrap:wrap;
								border:.1em #888 solid;
								border-radius:5px;
								padding:.2em 0;
								input[type="text"] {
									flex:1 0 1em;
									border:none;
									background-color:rgba(0,0,0,0);
									text-indent:.5em;
									font-size:.8em;
								}
								button {
									flex: 0 0 1.5em;
									border:none;
									background-color:rgba(0,0,0,0);
									outline:none;
									img {
										width: 100%;
										display: block;
									}
								}
							}//form
						}//.search-desktop
					}//.desktop-nav
				}//.desktop-menu
			}//header-wrap
		}
	}//header
	.login-button, .logout-button {
		cursor:hand;
		cursor:pointer;
		span {
			font-size:.8em;
			margin-left:.2em;
		}
	}
	.login-button {
		color:#19a319 !important;
	}
	.logout-button {
		color: tomato !important;
	}
	
	#search-box {
		display: none;
		width:100%;
		margin-bottom:.5em;
		
		form {
			
			@media only screen and (min-width : 64em) {
				display:none;
			}
			flex:1 0 1em;

			display: flex;
			flex-wrap:wrap;
			border:.1em #888 solid;
			padding:.2em 0;
			margin:0 1em;
			input[type="text"] {
				flex:1 0 1em;
				border:none;
				background-color:rgba(0,0,0,0);
			}
			button {
				flex: 0 0 2em;
				@media only screen and (min-width : 64em) {
					flex:0 0 1.5em;
				}
				border:none;
				background-color:rgba(0,0,0,0);
				outline:none;
				img {
					@media only screen and (min-width : 44em) {
						// width: 100%;
						
					}
					width: 100%;
					display: block;
					margin: 0 auto;
				}
			}
		}
	}//search-box
	nav {
		@media only screen and (min-width : 64em) {
			display: none;
		}
		}//nav
	#mobile-nav {
		margin:0;
		padding:0;
		display:none;
		flex-direction:column;
		width:100%;
		// height: 256px;
		// transition: all ease 1s;

		li {
		
			list-style-type: none;
			flex:0 0 4em;
			line-height:3.5em;
			// border-bottom:.1em #ddd solid;
			&:nth-child(2){

				#learning-sub-menu {
					display:none;
					flex-direction:column;
					padding:0;
					li {
						flex:0 0 2em;
						line-height:2em;
						a {
							font-size:.8em;
							padding-left:4em;
						}
					}
				}
			}
			a {
				text-decoration:none;
				color:#000;
				font-size:1em;
				font-weight:200;
				display:inline-block;
				padding-left:2em;
			}
			span {
				margin-left:.5em;
				margin-right: 2em;
				display:inline-block;
				width:.8em;
				height:.8em;
				// cursor:hand;
				// cursor:pointer;
				img {
					width:100%;
				}
			}
			.learning-flex {
				display: flex;
				flex-wrap: wrap;
				a {

				flex: 1 0 1em;
				}
				span {
				flex: 0 0 1em;
					
				}
			}
		}	//li
	}//mobile-nav

	main {

		.inner {
			@media only screen and (min-width : 75em) {
				width: 72em;
				margin: 0 auto;
			}

			& section:nth-child(1) {

				.main-photo {
					position:relative;
					z-index:5;
					background-image:url(../asset/photos/section1.jpg);
					background-position:center;
					background-size:120%;
					background-repeat:no-repeat;
					background-attachment: cover;
					@media only screen and (min-width : 450px) {
						width:100%;
						margin:0 auto;
					}
					@media only screen and (min-width : 44em) {
						height:20em;
						width:auto;
						margin:0;
					}
					@media only screen and (min-width : 64em) {
						
					}
					.photo-wrap {
						padding: 2em;
						position:relative;
						z-index:20;
						background: linear-gradient(180deg, rgba(0,0,0,.5), rgba(255,255,255,.5));
						
						@media only screen and (min-width : 44em) {
							height:20em;
							width:auto;
							margin:0;
							padding:0;
						}

						.about-box {
							@media only screen and (min-width : 44em) {
								padding:4em 0 0 2em;
							}	

							h3 {
								text-align:center;
								color:#fff;
								font-weight:600;
								font-family:dyslexia;
								font-size:1.5em;
								line-height:1.2em;
								@media only screen and (min-width : 44em) {
									text-align:left;
									font-size: 2em;
								}
								
							}
							.learn-more-wrap{
								width:100%;
								display:flex;
								align-items:center;
								margin-top:5em;
								@media only screen and (min-width : 44em) {
									align-items:flex-start;
									
								}
								a {
									margin: 0 auto;
									@media only screen and (min-width : 44em) {
										margin: 0;
									}
									text-decoration:none;
									padding:.5em 1em;
									border:.1em #FFAF02 solid;
									border-radius:.5em;
									font-size:.7em;
									color:#fff;
									background-color:#FFAF02;

								}
							}
						}//about-box
					}

				}
			}//section(1)

			& section:nth-child(2) {
				width:auto;
				margin-bottom:3em;
				@media only screen and (min-width : 44em) {
					display:flex;
					flex-wrap:wrap;
					width:100%;
				}
				.learning-box {
					height:7em;
					text-align:center;
					line-height:7em;
					.inspire, .dyslexia-desktop {
						width: 100%;
						height: 100%;
						display: none;
					}
					.inspire {
						background-color:#F26E22;
						position:relative;
						@media only screen and (min-width : 64em) {
							display: block;
						}
						h2 {
							margin:.8em 0 1em 1.5em;
							text-align:left;
							color: #fff;
							font-weight: 600;
						}
						.quote-content {
							width: 90%;
							margin: 0 auto;
							font-size: .9em;
							line-height:1.3em;
							// color: #888;
							font-family:dyslexia;
							color:#fff;
							i {
								display: block;
								margin: .5em 0;
								font-size: 1.3em;
								color:#f59056;
							}
						}
						.quote-person {
							position:absolute;
							bottom:1.5em;
							left:50%;
							transform:translateX(-50%);
							color:#fff;
							font-size: .7em;
							font-weight: 200;
							margin: 0;
							font-family:dyslexia;
							font-style:italic;
						}
					}
					.dyslexia-desktop:hover .flipper, .dyslexia-desktop.hover .flipper {
						transform: rotateY(180deg);
					}
					.dyslexia-desktop {
						@media only screen and (min-width : 64em) {
							
							display: block;
							perspective:1000;
						}
						.flipper{
							transition:.6s;
							transform-style: preserve-3d;
							position:relative;
							height: 100%;
							.front, .back, .training-front, .training-back {
								backface-visibility: hidden;
								position: absolute;
								top: 0;
								left: 0;
							}

							.front {
								z-index:2;
								transform: rotateY(0deg);

								width: 100%;
								height: 100%;
								display:flex;
								align-items:center;
								background-image:url(../asset/photos/dyslexia_photo.jpg);
								background-size:145%;
								background-repeat:no-repeat;
								background-position:center;
								.border {
									display:none;
									@media only screen and (min-width : 64em) {
										display:inline-block;
										border:2px #fff solid;
										border-radius:10px;
										width:92%;
										height:92%;
										margin:0 auto;
										background-image:none;
										background-color:rgba(0,0,0,0);
										text-align:left;
										h2 {
											color:#fff;
											font-weight:400;
											display:inline-block;
											margin-left:.5em;
											margin-top:4em;

										}
									}
								}
							}
							.training-front {
								z-index:2;
								transform: rotateY(0deg);

								width: 100%;
								height: 100%;
								display:flex;
								align-items:center;
								background-image:url(../asset/photos/training.jpeg);
								background-size:170%;
								background-repeat:no-repeat;
								background-position:center;
								.border {
									display:none;
									@media only screen and (min-width : 64em) {
										display:inline-block;
										border:2px #fff solid;
										border-radius:10px;
										width:92%;
										height:92%;
										margin:0 auto;
										background-image:none;
										background-color:rgba(0,0,0,0);
										text-align:right;
										h2 {
											color:#fff;
											font-weight:400;
											display:inline-block;
											margin-right:.5em;
											margin-top:4em;

										}
									}
								}
							}
							.back {
								transform: rotateY(180deg);	
								background-color:#555e77;
								width:100%;
								height:100%;
								.dyslexia-flip-box {
									color:#fff;
									h3 {
										margin:2em 0;
										font-weight:400;
										text-align:center;
									}
									p {
										width:90%;
										margin:0 0 3em 10%;
										font-size:.9em;
										font-weight:300;
										line-height:1.2em;
										text-align:left;

									}
									a {
										border-radius:5px;
										border:2px #fff solid;
										text-decoration:none;
										color:#fff;
										font-weight:300;
										padding:.4em;
										margin-left:7em;
									}
									& a:hover {
										border:2px #F26E22 solid;
										color:#F26E22;
									}
								}
							}
							.training-back {
								transform: rotateY(180deg);	
								background-color:#ac3954;
								width:100%;
								height:100%;
								.dyslexia-flip-box {
									color:#fff;
									h3 {
										margin:2em 0;
										font-weight:400;
										text-align:center;
									}
									p {
										width:90%;
										margin:0 0 3em 10%;
										font-size:.9em;
										font-weight:300;
										line-height:1.2em;
										text-align:left;

									}
									a {
										border-radius:5px;
										border:2px #fff solid;
										text-decoration:none;
										color:#fff;
										font-weight:300;
										padding:.4em;
										margin-right:7em;
									}
									& a:hover {
										border:2px #F26E22 solid;
										color:#F26E22;
									}
								}
							}
						}
					}// dyslexia-desktop
					
					.donation {
						display:none;
						@media only screen and (min-width : 64em) {
							display:block;
							padding:1em;
							margin-bottom:0;
							.donate-text {
								p {
									margin-bottom:1em;
								}
							}
						}
					}
					@media only screen and (min-width : 44em) {

						flex:1 0 49%;
					}
					@media only screen and (min-width : 64em) {
						display:flex;
						align-items:center;
						.donation-normal {
							display:none;
						}
						flex:1 0 20%;
						height: 15em;
						line-height:1;
					}
					
					span {
						font-size:2em;
						display:block;
						font-family:helvetica;
						font-weight:200;
						@media only screen and (min-width : 64em) {
							// display:flex;
							// align-items:center;
							display:none;
						}
						a {
							display:block;
							text-decoration:none;
							color:#fff;
						}
					}
				}//learning-box
			
				& :nth-child(2) span {
					background-color:#de88a5;
				}
				& :nth-child(3)  span {
					background-color:#7a9eaf;
				}
				& :nth-child(4) span {
					background-color:#655989;
				}
				& :nth-child(5) span {
					background-color:#3b6daf;
				}
				@media only screen and (min-width : 64em) {

						& :nth-child(2) span ,& :nth-child(3) span ,& :nth-child(4) span ,& :nth-child(5) span {
							height: 100%;
						}
				}
				
			}//section(2)

			& section:nth-child(3) {
				@media only screen and (min-width : 44em) {
					display:flex;
					flex-wrap:wrap;
					width:100%;
				}
				.news,.faq{
					@media only screen and (min-width : 44em) {
						flex:1 0 45%;
					}
					h3 {
						text-align: right;
						// margin-right: .5em;
						margin-right: 5%;
						font-family: helvetica;
						font-size:1em;
						font-weight: 500;
						margin-bottom: 2em;
						color:#888;
					}
					h2, p {
						width: 90%;
						margin: 0 auto;
						font-family: helvetica;
					}
					h2 {
						margin-bottom: 1em;
						color: #FFAF02;
						font-weight: 600;
					}
					
				}
				.news {
					a {
						display: inline-block;
						padding:.5em 1em;
						border:.1em #FFAF02 solid;
						border-radius:.5em;
						font-size:.7em;
						color:#fff;
						background-color:#FFAF02;
						text-decoration: none;
						margin-top:2em;
						margin-left: 5%;
					}
					p {
						color:#415E6C;
						font-size:0.8em;
					}
					margin-bottom:3em;
				}
				.faq {
					width:90%;
					@media only screen and (min-width : 44em) {
						padding:0;
						border:none;

					}
					margin:0 auto 3em auto;
					padding:5% 1%;
					border:.1em #FFAF02 solid;
					.faq-wrap{
						
						@media only screen and (min-width : 44em) {
							padding:2em .1em;
							width:90%;
							margin:0 auto;
							border:.1em #FFAF02 solid;

						
						}
					}
					a {
						text-decoration: none;
						color:#415E6C;
						font-size:.8em;
					}
					
				}
			}//section(3)

			& section:nth-child(4),section {
				.newsletter {
					flex:1 0 1em;
					padding: 3%;
					background-color:#F2F2F2;
					margin-bottom: 1em;
					h5 {
						font-weight: 300;
						margin: 0;
						text-indent: 1em;
					}
					p {
						font-size: .6em;
						font-style: italic;
						margin: .5em 0 2em 0;
						text-indent: 2.5em;
						color: #415E6C;
					}
					.newsletter-wrap{
						width:100%;
						display:flex;
						flex-wrap:wrap;
						align-items:center;
						form {
							width:95%;
							@media only screen and (min-width : 44em) {
								flex:1 0 1em;
							}
							// margin:0 auto;
							margin-left: .5em;
							input[type="text"] {
								width: 65%;
								padding:.5em;
								font-size: .8em;
								border:1px #fff solid;
								text-indent: 1em;
								font-weight: 300;
							}
							input[type="submit"] {
								width: 20%;
								padding:.5em;
								font-size: .8em;
								border:1px #FFAF02 solid;
								border-radius:.4em;
								background-color: #FFAF02;
								color: #fff;
								font-weight: 100;
							}
						}
						.facebook {
							display:none;
							@media only screen and (min-width : 44em) {
								margin:0 auto;
								flex:0 0 20%;
								display:flex;
								justify-content:center;
								align-items:center;
								border-left:1px #ccc solid;
								span {
									font-size:.8em;
									display:inline-block;
									color:#000;
								}
								a {
									margin-left:1em;
									color:#3b5998;
									font-size:2em;							
								}
							}

						}
					}
				}//newsletter

				.support {
					background-color: #F2F2F2;
					width: 100%;
					display: flex;
					justify-content: center;
					.image-slider {
						padding: 3em 0;
						display: flex;
						flex-wrap:wrap;
						width: 80%;
						margin-bottom: 0;
						.slick-track {
							align-items:center;
							display: flex;
							flex:1 0 25%;
						}
					
						.img-wrap {
							text-align: center;					
								outline: none;
							img {
								width: 60%;
								// max-width: 200px;
								display: inline-block;

							}
						}
						& button {
							background-color: #888;
							border-radius:50%;
						}
					}
				}

			}//section(4)
		}	
	}//main

	footer {

		.footer-wrap {

			display:flex;
			align-items:center;
			ul {
				// outline:1px red solid;	
				
				margin:2em auto 7em auto;
				padding:0;
			

				li {
					display:inline-block;
					list-style-type:none;
					a {
						text-decoration:none;
						font-size:.7em;
						color:#888;
					}
				}
				& li:nth-child(n+2) {
					margin-left:.5em;
				}
			}
		}

	}//footer
	.bottom-ids {
		width:100%;
		position: fixed;
		bottom: 0;
		// border:1px red solid;
		.inner {
			display:flex;
			flex-wrap:wrap;
			@media only screen and (min-width : 75em) {
				position:relative;
				width: 72em;
				margin: 0 auto;
			}
			#go-top {
				display:none;
				height:1.5em;
				outline:1px #888 solid;
				position:absolute;
				bottom:1em;
				left:3em;
				padding:1em;
				color:#888;
				font-size:.7em;
				cursor:pointer;
				cursor:hand;
			}//go-top
			#go-top:hover {
				color:#FBFAF2;
				background-color:#888;
				font-weight:300;
			}

			#reading-assist{
				// position:fixed;
				bottom:0;
				right:3%;
				width: 95%;
				.reading-assist-top {
					margin:0;
					display: flex;
					flex-wrap:wrap;
					justify-content:flex-end;
					.assist-box-wrap {
						background-color:#467BC1;
						cursor:hand;
						cursor:pointer;	
						flex:0 0 7em;
						display:flex;
						flex-wrap:wrap;
						margin-right:1em;
						
						.circle {
							flex:0 0 .8em;
							display:flex;
							align-items:center;
							padding-left:.5em;
							img{
								width:100%;
							}
						}
						.assist-box {
							flex:0 0 5em;
							font-size:.8em;
							color:#fff;
							font-weight:200;
							padding:1em;
							p {
								margin:0;
							}
						}//assist-box
					}//assist-box-wrap
				}//reading-assist-top

				.reading-assist-bottom {
					display:none;
					margin:0;
					width: 100%;
					flex-wrap:wrap;
					justify-content:flex-end;

					.empty {
						flex:1 0 1em;
						height:1em;
					}

					.assist-detail {
						background-color:#fff;
						flex:0 0 15em;
						display: flex;
						flex-wrap:wrap;
						// padding-left:1em;


						.text-size {
							flex:1 0 50%;
							padding-left:1em;
							p {
								font-size:.8em;
								color:tomato;
								text-align:center;

							}
							ul {
								display: flex;
								flex-wrap:wrap;
								padding:0;
								li {
									flex: 1 0 20%;
									margin: 0 2px;
									list-style-type: none;
									text-align:center;
									line-height:21px;
									background-color:#f4f2f1;
									border-radius: 4px;
									box-shadow: inset 0 5px 5px -5px rgba(0,0,0,.48);
									cursor:hand;
									cursor:pointer;
								}
								& li:nth-child(1) {
									font-size:.7em;
								}
								& li:nth-child(2) {
									font-size:.9em;
								}
								& li:nth-child(3) {
									font-size:1.1em;
								}

								
							}

						}
						.assist-on-off {
							flex: 0 0 40%;
							display: flex;
							align-items:center;
								
							.on-off-wrap {
								border:2px #888 solid;
								background-color:#888;
								border-radius:5px;
								width: 70%;
								height: 2.5em;
								margin: 0 auto;
								display: flex;
								flex-wrap:wrap;
								.on-box,.off-box {
									flex:1 0 40%;
									margin: 1px;
									cursor:pointer;
									cursor:hand;
									p {
										margin:0;
										line-height:40px;
										text-align:center;
									}
								}
								.on-box {
									border:1px #fff solid;
									background-color:#fff;
									border-radius:3px;
									color:tomato;
									
								}
								.off-box {
									background-color:rgba(0,0,0,0);
									color:#fff;
									font-weight:100;
									
								}
								
							}
						}
					}
					
				}
				
			}//reading-assist
		}
	}//bottom-ids

}//body
