.tomato {
	color:tomato !important;
}

.loggedin {
	margin-top:5em;
	display:flex;
	flex-direction:column;
	.squire {
		flex:0 0 7em;
		margin-top:1em;
		margin-bottom:3em;
		border:2px #E59C00 solid;
		border-radius:5px;
		display:flex;
		align-items:center;
		text-decoration: none;
		span {
			width:100%;
			text-align:center;
			font-size:2em;
			color:#E59C00;
			font-weight:200;
		}
		transition:ease all .2s;
	}
	.squire:hover {
		border:2px #E59C00 solid;
		background-color:#E59C00;
		span {
			color:#fff;
			font-weight:100;
		}
	}
}
//----------------loggedin------------------------------------


//----------------member--------------------------------------
.admin-creater {
	width:2.5em;
	height:2.5em;
	position:fixed;
	z-index:20;
	bottom:10em;
	right:3em;
	@media only screen and (min-width : 64em) {
	
		left:50%;
		transform:translateX(-50%);
	}
	display:block;
	img {
		width:100%;
	}
}
.member-container {
	margin-bottom:2em;
	border-left:2px #E59C00 solid;
	padding-left:1em;
	position:relative;
	.member-name {
		margin-bottom:1em;
		color:#000;
	}
	span {
		color:#444;
	}
	.margin-bottom {
		margin-bottom:.5em;
	}
	.margin-bottom2 {
		margin-bottom:1em;
	}
	color:#888;

	.user-delete {
		padding:.5em;
		color:#F44336;
		border:1px #F44336 solid;
		font-weight:300;
		background-color:rgba(0,0,0,0);
	}
	.user-delete:hover {
		color:#FBFAF2;
		border:1px #F44336 solid;
		font-weight:100;
		background-color:#F44336;
	}
		
}

//================createMember--------------------------------

.member-form {
	background-color:rgba(0,0,0,0) !important;

	.margin-bottom {
		margin-bottom:3em !important;
	}
	textarea {
		padding:.7em;
		margin-bottom: 1em;
		border: none;
		font-size: .8em;
		border-bottom: 1px #415E6C solid;
		background-color: rgba(255, 255, 255, 0.1);
		outline: none;
		font:inherit;
		color: #555;
		font-weight: 300;
		textarea::placeholder {
				color:#415E6C;
			}
		&:focus {
			border-bottom:1px tomato solid;
		}
	}
	select {
		width: 30%;
		border:1px #415E6C solid;
		padding:.8em;
		margin-top:1em;
		margin-bottom: 2em;
		background-color: rgba(0,0,0,0);
		font-size:.8em;
		height: 2em;
		outline: none;

	}
}
.information-pack {
	border:2px orange solid;
	display:inline-block;
	padding:0;
	// margin-left:-1em;
	p {	
		font-style: italic;
		padding:0 1em;
		font-size:.8em;
		a {
			color: tomato ;
		}
	}
}

//=============================errors-alert==========================

.alert {
	border:2px orange solid;
	display:inline-block;
	margin-left:2em;
	margin-bottom:2em;
	ul {
		margin: 0;
		padding:1em;
		li {
			list-style-type: none;
			color: red;
			font-size:.8em;
		}
		
	}
}

h3 {
	font-size: 1.2em !important;
}
.training {
	span {
		font-size:.9em;
		margin-top: 1em;
		display: block;
	}
	p {
		font-size: .9em;
		margin-top: 2em;
	}
	.info-training {
		border:none !important;
		display: block;
		margin-bottom:.5em;
		i {
			display: inline-block !important;
			margin-right: .2em;
			color: #415E6C;
		}
	}
	.info-training:hover {
		text-decoration: underline;
	}
	.hours-course {
		width: 100%;
		display: flex;
		flex-wrap:wrap;
		align-items: center;
		p {
			margin-bottom: 1em !important;
		}
		.hour-content {
			flex: 1 0 1em;
		}
		.hour-number {
			flex: 0 0 2em;
			margin-left: .5em !important;
			text-align: right;
		}

	}
}

