
.navigation {
	
	padding: 1em;
	@media only screen and (min-width : 64em) {
		width:54%;
		display:inline-block;
		vertical-align:top;
	}
	a, span {
		color:#ccc;
		text-decoration:none;
	}
	span {
		font-size:.8em;
		p {
			display:inline-block;
			font-size:.8em;
		}
	}
	a {
		font-size: 1em;
	}
	.page-nav {
		text-decoration:none;
		color:#E59C00;
		font-size:.9em;

	}
	

	.container {
		flex:1 0 1em;
		padding:1em;

		h1 {
			margin:0 0 1em 0;
			color:#555;
			p {
				display:inline-block;
				font-size:.7em;
			}
			// font-size:3em;
			// font-weight:600;
		}
		.content {
			flex:1 0 1em;
			padding:0 1em;
			// position:relative;
			.about-content {
				margin-bottom:5em;
				h3,p {
					color:#415E6C;
				}
				h3 {
					margin:2em 0 2em 0;
				}
				p {
					margin:0 0 .5em 0;
					line-height:1.5em;
					
				}
			}
		}
	}
}
section.pages{
		@media only screen and (min-width : 64em) {
			width:40%;
			display:inline-block !important;
			margin-top:7.5em;
		}
	.snz-intro {
		flex:1 0 1em;
		
		margin:0 auto 1em auto;
		padding:2em;
		text-align:center;
		background-color:#B27815;
		color:#fff;
		h4 {
			font-weight:200;
			// font-size:1em;
			span {
				font-weight:500;
				font-size:1.1em;
			}
		}
	}
	
}
.donation {
		flex:1 0 1em;
		background-color:#3b6daf;
		padding:2em;
		text-align:center;
		margin-bottom: 1em;
		.donate-box {
			color:#fff;
			.donate-text {
				display:inline-block;
				h2,p {
					text-align:left;
				}
				h2 {
					width:100%;
				}
				p {
					width:100%;
					font-size:.8em;
					font-weight:100;
					margin-bottom:4em;

				}
			}
			.donate-img {
				width:20%;
				margin:0 auto 2em auto;
				img {
					width:5em;
				}
			}
			.donate-button{
				text-align:center;
				a {
					display:inline-block;
					font-size:.8em;
					text-decoration:none;
					border:.1em #FFAF02 solid;
					border-radius:5px;
					background-color:#FFAF02;
					color:#fff;
					padding:.7em 1.2em;
				}
			}
		}
	}